<template>
  <hs-dialog ref="Dialog" v-model="currentValue">
    <div class="hs-filter" @click.stop>
      <div class="header">
        <p @click="_close">{{$t('base.Cancel')}}</p>
        <p :class="[canReset ? 'canReset' : 'cannotReset']" @click="_reset">{{$t('base.ResetFilter')}}</p>
        <p @click="_confirm">{{$t('base.Confirm')}}</p>
      </div>
      <div class="details">
        <hs-scroll class="table-view" :data="currentFilters">
          <ul>
            <div class="item" v-for="(item,index) in currentFilters" :key="index">
              <p class="header">{{item.Name}}</p>
              <div class="options">
                <div v-for="(itemChild,indexChild) in item.Items" :key="indexChild" @click="[itemChild.select =! itemChild.select, _setCanReset()]"
                  class="option"
                  :class="{actived: itemChild.select}">
                  <p>{{itemChild.Value}}</p>
                  <div class="logo" style="background-image: url('static/image/base/filter/train-selected.png')"></div>
                </div>
              </div>
            </div>
          </ul>
        </hs-scroll>
      </div>
    </div>
  </hs-dialog>
</template>
<script type="text/ecmascript-6">
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsDialog from 'base/hs-dialog/hs-dialog'
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    filters: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  computed: {
    currentValue: {
      get: function() {
        return this.value
      },
      set: function(val) {
        return this.$emit('input', val)
      }
    }
  },
  watch: {
    currentValue(val) {
      if (val) {
        this.currentFilters = [...this.filters]
        this._setCanReset()
      }
    }
  },
  data() {
    return {
      currentFilters: [],
      canReset: false
    }
  },
  methods: {
    /** 关闭 */
    _close() {
      this.$refs.Dialog.close()
    },
    /** 确认 */
    _confirm() {
      this.$emit('confirmFliter', this.currentFilters)
      this._close()
    },
    /** 重置 */
    _reset() {
      for (let filter of this.currentFilters) {
        for (let item of filter.Items) {
          item.select = false
        }
      }
      this._setCanReset()
    },
    /** 是否可清除筛选 */
    _setCanReset() {
      let res = false
      for (let filter of this.currentFilters) {
        for (let item of filter.Items) {
          if (item.select) {
            res = true
          }
        }
      }
      this.canReset = res
    }
  },
  components: {
    HsScroll,
    HsCell1,
    HsDialog
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-filter
  position absolute
  width 100%
  left 0
  bottom 0
  background-color $color_white
  border-radius 10px 10px 0 0
  max-height 70%
  overflow hidden
  >.header
    @extend .flex-row
    @extend .flex-between
    width 100%
    height 45px
    border-bottom 1px solid $color_gray_layer
    padding 0 15px
    >p
      font-size 0.8rem
    >.cannotReset
      color $color_gray_layer
      user-select none
    >.canReset
      padding 5px
      background-color $color_gray_background_0
  >.details
    width 100%
    height 400px
    font-size 0.8rem
    .table-view
      width 100%
      height 100%
      overflow hidden
      .item
        width 100%
        >.header
          @extend .flex-row
          @extend .flex-start
          width 100%
          height 40px
          padding 0 15px
          font-size 1rem
        >.options
          @extend .flex-row
          @extend .flex-start
          flex-wrap wrap
          width 100%
          >.option
            width 50%
            height 36px
            padding 0 15px
            margin-bottom 10px
            position relative
            >p
              @extend .flex-center
              width 100%
              height 100%
              border 1px solid $color_gray_layer
              border-radius 2px
              color $color_black_text_1
            >.logo
              width 20px
              height 20px
              background-size 100% 100%
              position absolute
              right 15px
              bottom 0
              z-index 1
              display none
          >.actived
            >p
              color $color_theme
              border 1px solid $color_theme
            >.logo
              display block
</style>



